}

.main {
  border: 4px solid black;
  background-color: white;
  height: 97vh;
  width: 74em;
  border-top: none;
}

.commonlogo {
  height: 4em;
}

.logo {
  margin-top: 2em;
  margin-left: 25px;
}

.orgname {
  margin-left: 1em;
  line-height: 0;
  font-family: serif;
}

.navvbar {
  height: 83vh;
}

.eachBox {
  height: 3em;
  align-self: center;
}

.btnn {
  padding-top: 7em;
}

.eachBtn {
  border-radius: 7px;
  font-family: Courier New;
  color: #75aaee;
  font-size: 18px;
  background: #1f153c;
  padding: 3px 20px;
  border: solid #598ba8 2px;
  text-decoration: none;
  font-weight: bold;
}

.eachBtn:hover {
  background: #e20b0b;
  color: #edede7;
  text-decoration: none;
  font-weight: bold;
}

a {
  text-decoration: none;
  color: white;
}
/* -------------teacher profile ------------------ */
.picture {
  border: 1px solid red;
  display: flex;
  justify-content: center;
  width: 9em;
  height: 8em;
  position: relative;
  bottom: 52px;
  left: 4em;
  z-index: 20;
  border-radius: 50%;
}

.submitbtnleft {
  position: relative;
  top: 4em;
  left: 17em;
}

.submitbtnright {
  position: relative;
  top: 61px;
  right: 15em;
}

.leftblock {
  width: 15em;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 3em;
  position: relative;
  left: 5em;
}

.rightblock {
  width: 15em;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 3em;
  position: relative;
  left: 14em;
}

.form-group {
  margin-bottom: 1em;
}

.leftDiv {
  height: 55vh;
  width: 29vw;
  display: inline-block;
}

.rightDiv {
  height: 55vh;
  width: 34vw;
  float: right;
}
.uppImg {
  width: 69vw;
  height: 35vh; 
}
